.shop-container{
    display: grid ;
    grid-template-columns: 4fr 1fr; 
}
.products-container{
    display: grid;
    grid-template-columns: repeat(3,1fr) ;
    gap: 40px;
    margin: 100px 50px;
}

.cart-container{
    background: rgba(255, 153, 0, .3);
    padding:1rem 2rem;
}
@media screen and (max-width: 1100px){
    .products-container{
        grid-template-columns: repeat(2,1fr);
    }
}